import neilModal from "@/components/neil-modal/neil-modal.vue"
import cmdNavBar from "@/components/cmd-nav-bar/cmd-nav-bar.vue"
import cmdPageBody from "@/components/cmd-page-body/cmd-page-body.vue"
import cmdTransition from "@/components/cmd-transition/cmd-transition.vue"
import cmdCelItem from "@/components/cmd-cell-item/cmd-cell-item.vue"
import cmdAvatar from "@/components/cmd-avatar/cmd-avatar.vue"

export default {
	components: {
		cmdNavBar,
		cmdPageBody,
		cmdTransition,
		cmdCelItem,
		cmdAvatar,
		neilModal
	},
	onLoad() {
		this.checkLogin();
		this.initUserinfo();

	},
	data() {
		return {
			staffInfo: null,
			show: false,
			title: '',
			subdata: '',
			inputname: ''
		};
	},
	methods: {
		/**
		 * 点击触发
		 * @param {Object} type 跳转页面名或者类型方式
		 */
		fnClick(type) {
			if(type=='no'){
				uni.showToast({
					title: '禁止修改'
				});
			}else{
				if (type == 'mobile') {
					uni.navigateTo({
						url: '/pages/index/forget'
					})
				} else {
					uni.navigateTo({
						url: '/pages/index/upStaffInfo?type=' + type
					})
				}	
			}

		},
		initUserinfo() {
			uni.request({
				url: this.apiServer + '/staff/staff/show',
				method: 'GET',
				data: {
					st_id: uni.getStorageSync('STID')
				},
				success: res => {
					let data = res.data;
					this.staffInfo = data.data
				},
				fail: () => {},
				complete: () => {}
			});
		},
		updateUserInfo(param, title) {
			this.title = title;
			this.inputname = param;
			this.show = true;
		},
		bindBtn() {
			uni.showToast({
				title: this.subdata
			});
		},
		closeModal() {
			this.show = false;
		}
	}
}
